<template>
     <input :value="modelValue" :placeholder="placeholder" @input="handerInput" @blur="handerBlur" />
</template>

<script lang="ts" setup>
import { ref,inject } from 'vue';
const props = defineProps({
    modelValue: {
        type: String,
        default: ''
    },
    placeholder: {
        type: String
    }
})
const fn:any=inject('fn');
const emit = defineEmits(["update:modelValue"]);
const handerInput = (e: Event) => {
    const target = e.target as HTMLInputElement;
    emit("update:modelValue", target.value);
}
const handerBlur = (e: Event) => {
    const target = e.target as HTMLInputElement;
    fn(target.value)
}
</script>



<style scoped>
input {
    padding: 6px 11px;
    color: rgba(0, 0, 0, 0.88);
    font-size: 14px;
    border-radius: 6px;
    border: 1px solid #ccc;
}

input:hover {
    border-color: #4096ff;
}

input:focus,
input:focus-within {
    border-color: #1677ff;
    outline: 0;
}
</style>